/* Scss Document */

@charset "utf-8";

@mixin o-tag {
    color: $white;
    margin: 0 auto;
    text-align: center;
}


.o-tag {
    &--rad4 {
        @include o-tag;
        font-size: $font13;
        height: 24px;
        width: 72px;
        line-height: 23px;
        border-radius: 4px;
        background-color: $lightgrey;
    }
    &--rad12 {
        @include o-tag;
        font-size: $font13;
        height: 24px;
        width: 72px;
        line-height: 23px;
        border-radius: 12px;
        background-color: $lightgrey;
    }
}