/* Scss Document */

@charset "utf-8";
.body__notice {
    & .notice {
        background-color: $white;
        transition: margin-top 0.3s;
        height: 0px;
        text-align: center;
        vertical-align: middle;
        margin-top: -48px;
        & > a {
            height: 100%;
            justify-content: center;
        }
        &.u-mt-0{
            height: 48px; 
            margin-top: 0px;
        }
    }
}

.body__notice--info{
    & .notice {
        background-color: $green--light;
        transition: all 0.3s ease-in-out;
        height: 0px;
        vertical-align: middle;
        margin-top: -100px;
        overflow-y: hidden;
        &.u-mt-0{
            height: 48px; 
            margin-top: 0px;
        }

        &__row{
            display: flex;
            align-items: center;
            flex-wrap: wrap;
            // min-height: 60px;
            padding-left: 12px;
            padding-right: 12px;
            max-width: 719px;
            margin-right: auto;
            margin-left: auto;
            padding-top: 6px;
            padding-left: 12px;
            padding-right: 12px;
            padding-bottom: 6px;
            position: relative;
        }
        &__title{
            flex-shrink: 1;
            color: $black;
            word-break: break-all;
            margin-right: 24px;
        }
        &__detail{
            flex-shrink: 0;
            text-align: right;
            // margin-left: auto;
            // margin-right: 12px;
            cursor: pointer;
            width: 95%;
        }
        &__message{
            width: 100%;
            padding: 12px;
            color: $lightgrey;
            font-size: $font13;
            box-sizing: border-box;
            max-width: 719px;
            margin-right: auto;
            margin-left: auto;            
        }
        &__close {
            color: $lightgrey--light;
            position: absolute;
            top: 5px;
            right: 5px;
        }
    }    
}