/* Scss Document */

@charset "utf-8";
@import "./_grid";

@mixin o-message {
    box-sizing: border-box;
    border-radius: 10px;
    padding: 10px;
    display: inline-flex;
    width: 100%;
    padding-right: 34px;
    & span,p{
        margin-left: 10px;
        color: $black;
    }
}

.o-message {
    &--alert {
        @include o-message;
        background-color: $orange--light;
        & i {
            color: $orange;
        }
    }
    &--danger {
        @include o-message;
        background-color: $red--light;
        & i {
            color: $red;
        }
    }
    &--info {
        @include o-message;
        background-color: $green--light;
        & i {
            color: $green;
        }
    }
    &--help {
        @include o-message;
        background-color: $blue--light;
        & i {
            color: $blue;
        }
    }
    &__title{
        font-weight: bold;
        margin-left: 10px;
    }
}

o-errmessage--md {}

@mixin o-noticemessage {
    box-sizing: border-box;
    border-radius: 10px;
    padding: 15px;
    display: inline-flex;
    flex-direction: column;
    position: fixed;
    top: 99px;
    right: -300px;
    width: 300px;
    transition: all 0.3s ease-in-out;
    z-index: 3;
    height: 110px;
    overflow-y: hidden;
    // transition: height 0.3s ease;
}

.o-noticemessage {
    &--alert {
        @include o-noticemessage;
        background-color: $orange--light;
        & .o-noticemessage__title i {
            color: $orange;
        }
    }
    &--danger {
        @include o-noticemessage;
        background-color: $red--light;
        & .o-noticemessage__title i {
            color: $red;
        }
    }
    &--info {
        @include o-noticemessage;
        background-color: $green--light;
        & .o-noticemessage__title i {
            color: $green;
        }
    }
    &--help {
        @include o-noticemessage;
        background-color: $blue--light;
        & .o-noticemessage__title i {
            color: $blue;
        }
    }
    &__close {
        color: $lightgrey--light;
        position: absolute;
        top: 5px;
        right: 5px;
    }
    &__more {
        position: absolute;
        top: 33px;
        right: 6px;
        & button{
            border-color: $yblue;
            border-width: 2px;
            border-style: solid;
            background-color: $yblue;
            height: 28px;
            width: 28px;
            min-width: 28px;    
            &:hover{
                background-color: $white;
                & > i{
                    color: $yblue;   
                }    
            }
        }
        & i{
            color: $white;   
        }
    }    
    &__title {
        color: $black;
        display: inline-flex;
        flex-direction: row;
        align-items: center;
        min-height: 100px;
        flex-wrap: wrap;
        & > div {
            margin-left: 10px;
            margin-right: 16px;
            display: block;
            height: 60px;
            overflow: hidden;
            width: 220px;
            // text-overflow: ellipsis;
            // white-space: nowrap;
        }
    }
    &__detail{
        width: 100%;
        text-align: right;
        display: block;
        cursor: pointer;    
    }
    &__message {
        color: $lightgrey;
        margin-left: 34px;
        font-size: $font13;
        & > div {
            word-break: break-all;
            padding-top: 10px;            
            padding-bottom: 15px;
        }
    }
    &__animation {
        top: 99px;
        right: 24px;
        min-height: 123px;
    }
}

@mixin o-errormessage {
    color: $red;
}

.o-errormessage {
    &--sm {
        @include o-errormessage;
        font-size: $font13;
    }
    &--md {
        @include o-errormessage;
        font-size: $font15;
    }
    &--lg {
        @include o-errormessage;
        font-size: $font17;
    }
}

// brekpoint sample
////////// Phone //////////
@media (max-width: $grid-tablet-breakpoint - 1) {
    .o-errormessage {
        &--sm {
            @include o-errormessage;
            font-size: $font13;
        }
        &--md {
            @include o-errormessage;
            font-size: $font13;
        }
        &--lg {
            @include o-errormessage;
            font-size: $font15;
        }
    }
}

////////// Tablet //////////
@media (min-width: $grid-tablet-breakpoint) and (max-width: $grid-desktop-breakpoint - 1) {
    .o-errormessage {
        &--sm {
            @include o-errormessage;
            font-size: $font13;
        }
        &--md {
            @include o-errormessage;
            font-size: $font13;
        }
        &--lg {
            @include o-errormessage;
            font-size: $font15;
        }
    }
}

@media (min-width: $grid-desktop-breakpoint) and (max-width: $grid-menudesktop-breakpoint - 1) {
    .body.is-sidebar-show {
        .o-errormessage {
            &--sm {
                @include o-errormessage;
                font-size: $font13;
            }
            &--md {
                @include o-errormessage;
                font-size: $font13;
            }
            &--lg {
                @include o-errormessage;
                font-size: $font15;
            }
        }       
    }
}
