/* Scss Document */

@charset "utf-8";

.mdl-dropdown {
    position: relative;
    cursor: pointer;
    width: 304px;
    & .mdl-button {
        display: flex;
        align-items: center;
        justify-content: space-between;
        width: 100%;
        height: 60px;
        box-sizing: border-box;
        border: 1px solid rgba(0, 0, 0, .15);
        cursor: pointer;
        padding-left: 24px;
        & > input {
            width: 100%;
            height: 100%;
            border-style: none;
            font-size: $font15;
        }
        &:hover {
            background-color: rgba(158, 158, 158, 0);
        }
        &:hover > input {
            background-color: rgba(158, 158, 158, 0);
        }
    }        
    & > .mdl-menu__container{
        top: -8px !important;
        width: 100%;
        & .mdl-menu {
            max-height: 300px;
            overflow-y: auto;
            position: relative;
            &__item{
                padding-left: 24px;
                height: 32px;
                line-height: 32px;
                font-size: $font15;
            }
        }
    }
}

.dropdown {
    &__prefecture {
        & .mdl-button {
            & > input {
                width: 100px;
            }
        }
        & ul.mdl-menu {
            width: 160px;
        }
    }
    &__gyosyu , &__yuuko {
        & .mdl-button {
            & > input {
                width: 300px;
            }
        }
        & ul.mdl-menu {
            width: 360px;
        }
    }
    &.is-invalid{
        & .mdl-textfield__title{
            color: $red;
        }
        & select{
            border-color: $red;
        }
    }
    & .o-errormessage--md{
        font-size: $font13;
    }
}

.mdl-dropdownmenu {
    position: relative;
    cursor: pointer;
    & .mdl-button {
//        height: 60px;
//        border: 1px solid rgba(0, 0, 0, .15);
        cursor: pointer;
        & > input {
            width: 100px;
            border-style: none;
        }
        &:hover {
            background-color: rgba(158, 158, 158, 0);
        }
        &:hover > input {
            background-color: rgba(158, 158, 158, 0);
        }
    }
    > .mdl-menu__container{
        margin-top: 30px;
        
        & > ul{
            z-index: 10001;
            max-height: 300px;
            overflow-y: visible;
            position: relative;
            width: 250px;
        } 
    }
}